
import { defineComponent } from "vue";
import * as API from "../../API";
import type { idCard } from "../../API";

export default defineComponent({
  data() {
    return {
      name: "",
      description: "",
      manacost: 0,
      type: "" as "unit" | "strategy" | "nebula" | "equipment" | "event",
      discardAfterUser: false,
      hp: 0,
      armor: 0,
      movement: 0,
      range: 0,
      tp: 0,
      effect: "" as any,
      up: "" as any,
      left: "" as any,
      right: "" as any,
      down: "" as any,
      id: "",

      cards: [] as idCard[],

      setCard: "",
      feedbackState: "",
      editingCard: false,
    };
  },
  async mounted() {
    try {
      this.cards = await API.getCards();
      console.log(this.cards);
    } catch (e) {
      console.log({ error: e });
    }
  },
  methods: {
    async createCard() {
      let newCard = {
        name: this.name,
        id: this.id ? this.id : this.name + "id",
        description: this.description,
        manacost: this.manacost,
        type: this.type,
        discardAfterUser: this.discardAfterUser,
        hp: this.hp,
        armor: this.armor,
        movement: this.movement,
        range: this.range,
        tp: this.tp,
        effect: this.effect,
        up: this.up,
        left: this.left,
        right: this.right,
        down: this.down,
      };
      console.log({ newCard: newCard });
      try {
        await API.setCard(newCard);
        this.feedbackState = "alert alert-success";
        this.setCard = "Karte wurde gespeichert";
        setTimeout(() => (this.setCard = ""), 2000);
        this.clearInputs();
      } catch (e) {
        console.log({ error: e });
        this.feedbackState = "alert alert-danger";
        this.setCard = "Karte konnte nicht gespeichert werden";
        setTimeout(() => (this.setCard = ""), 2000);
      } finally {
        try {
          setTimeout(async () => (this.cards = await API.getCards()), 1000);
        } catch (e) {
          console.log({ error: e });
        }
      }
    },
    editCard(card: idCard) {
      this.editingCard = true;
      this.name = card.name;
      this.description = card.description;
      this.manacost = card.manacost;
      this.type = card.type;
      this.discardAfterUser = card.discardAfterUser;
      this.hp = card.hp;
      this.armor = card.armor;
      this.movement = card.movement;
      this.range = card.range;
      this.tp = card.tp;
      this.id = card.id;
      this.effect = card.effect;
      this.up = card.up;
      this.left = card.left;
      this.right = card.right;
      this.down = card.down;
    },
    async deleteCard(card: idCard) {
      if (window.confirm(`Sicher das du "${card.name}" löschen möchtest ? `))
        try {
          await API.deleteCard(card.id);
        } catch (e) {
          console.log({ error: e });
        } finally {
          try {
            setTimeout(async () => (this.cards = await API.getCards()), 1000);
          } catch (e) {
            console.log({ error: e });
          }
        }
    },
    clearInputs() {
      this.editingCard = false;
      this.name = "";
      this.description = "";
      this.manacost = 0;
      this.type = "" as "unit" | "strategy" | "nebula" | "equipment" | "event";
      this.discardAfterUser = false;
      this.hp = 0;
      this.armor = 0;
      this.movement = 0;
      this.range = 0;
      this.tp = 0;
      this.id = "";
      this.effect = "";
      this.up = "";
      this.left = "";
      this.right = "";
      this.down = "";
    },
  },
});
