
import { defineComponent } from "vue";
import * as API from "../../API";

export default defineComponent({
  data() {
    return {
      createDeck: false,
      editDeck: false,
      decks: [] as string[],
      currentDeck: "",
      newDeckName: "",
    };
  },
  async mounted() {
    try {
      this.decks = await API.getDecks();
    } catch (e) {
      console.log({ error: e });
    }
  },
  methods: {
    openDeck(Deck: string) {
      console.log(Deck);
    },
    createNewDeck(newDeckName: string) {
      console.log(newDeckName);
    },
  },
});
